<template>
  <div class="order-wrapper">
    <div class="order">
      <div class="header">
        <div class="header-top">订单管理</div>
        <div class="nav">
            <div class="nav-item" v-for="(item,index) in navItem" :class="[item.class,{'selected':item.isSelected}]" @click="changeSelect(index)">
              <div class="nav-item-icon"></div>
              <div class="nav-item-title">
                {{item.title}}
              </div>
            </div>
        </div>
      </div>
      <div class="content">
        <div class="order-list" v-for="item in orderItem">
          <div class="list-top">
            <div class="list-top-left">
              <p>订单编号{{item.orderNo}}</p>
              <p>下单时间{{item.time}}</p>
            </div>
            <div class="list-top-rigth">
              <div class="paid" v-if="item.serial=='paid'">已付款</div>
              <div class="paid" v-if="item.serial=='confirmation'">已付款</div>
              <div class="obligation" v-if="item.serial=='obligation'">待付款</div>
              <div class="cancel" v-if="item.serial=='cancel'">已取消</div>
            </div>
          </div>
          <div class="list-main">
            <div class="list-main-left">
              <img :src="item.img">
            </div>
            <div class="list-main-right">
              <h4 class="title">{{item.name}}</h4>
              <p class="passenger">成人*{{item.passengers.adult}}&nbsp;{{item.passengers.adult > 0 ? ('小童*'+item.passengers.adult) : ''}}</p>
              <p class="time">{{item.time}}</p>
            </div>
            <div class="price">
              HK${{item.price}}
            </div>
          </div>
          <div class="list-bottom">
            <div class="paid" v-if="item.serial=='paid'">
              <a href="javascript:;">评论</a>
              <a href="javascript:;">电子票</a>
            </div>
            <div class="paid" v-if="item.serial=='finish'">已付款</div>
            <div class="confirmation" v-if="item.serial=='confirmation'">
              电子票正在确认中
            </div>
            <div class="obligation" v-if="item.serial=='obligation'">
              <a href="javascript:;">删除</a>
              <a href="javascript:;">付款</a>
            </div>
            <div class="cancel" v-if="item.serial=='cancel'">
              <a href="javascript:;">删除</a>
              <a href="javascript:;">再次购买</a>
            </div>
          </div>
        </div>
      </div>
      <v-footer></v-footer>
    </div>
  </div>
</template>

<script>
  import vFooter from '../common/footer/footer';
  let baseData = {
    "navLists": [
      {
        "title": "全部订单",
        "class": "all-orders",
        "isSelected": true
      },
      {
        "title": "待付款",
        "class": "obligation",
        "isSelected": false
      },
      {
        "title": "已付款",
        "class": "paid",
        "isSelected": false
      },
      {
        "title": "已取消",
        "class": "cancelled",
        "isSelected": false
      }
    ],
    "orderLists": [
      {
        "serial": "paid",
        "orderNo": "12345678902",
        "orderTime": "2017-06-01 13:48",
        "img": "https://static.vux.li/demo/3.jpg",
        "name": "巴黎双飞五日游",
        "passengers": {
          "adult": 2,
          "child": 1
        },
        "time": "2017-06-18 09:20",
        "price": "3264"
      },
      {
        "serial": "obligation",
        "orderNo": "12345678905",
        "orderTime": "2017-02-01 11:28",
        "img": "https://static.vux.li/demo/2.jpg",
        "name": "西安古城一日游门票",
        "passengers": {
          "adult": 4,
          "child": 3
        },
        "time": "2017-05-11 08:10",
        "price": "126"
      },
      {
        "serial": "confirmation",
        "orderNo": "12345678905",
        "orderTime": "2017-02-01 11:28",
        "img": "https://static.vux.li/demo/2.jpg",
        "name": "西安古城一日游门票",
        "passengers": {
          "adult": 4,
          "child": 3
        },
        "time": "2017-05-11 08:10",
        "price": "126"
      },
      {
        "serial": "cancel",
        "orderNo": "12345678905",
        "orderTime": "2017-02-01 11:28",
        "img": "https://static.vux.li/demo/2.jpg",
        "name": "西安古城一日游门票",
        "passengers": {
          "adult": 4,
          "child": 3
        },
        "time": "2017-05-11 08:10",
        "price": "126"
      }
    ]
  }
  export default {
    name: 'order',
    data () {
      return {
        navItem: baseData.navLists,
        orderItem: baseData.orderLists
      }
    },
    components: {
      vFooter
    },
    methods: {
      changeSelect (index) {
        if(this.navItem[index].isSelected == true){
          return;
        }else{
          this.navItem.forEach(function(val, indexs){
            val.isSelected = false;
          });
          this.navItem[index].isSelected = true;
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
   @import "../../common/less/mixin.less";
   .order-wrapper{
    padding-bottom: 57px;
   }
  .order{
    height: 100%;
    background-color: #f8f8f8;
    .header{
      .header-top, .nav{
        border-bottom: 1px solid #e8e8e8;
        margin-bottom: 9px;
        background-color: #fff; 
      }
      .header-top{
        height: 64px;
        line-height: 64px;
        text-align: center;
        font-weight: bold;
        color: #363636;
      }
      .nav{
        display: flex;
        text-align: center;
        .nav-item{
          flex:1;
          height: 68px;
          margin: 0;
          padding: 0;
          font-size: 12px;
          color: #666;
          &.selected{
            color:#f36f69;
            border-bottom: 1px solid #fe6f69;
            margin-bottom: -1px;
          }
          .nav-item-icon{
            display: block;
            width: 38px;
            height: 38px;
            border-radius: 50%;
            background-color: #f3f3f3;
            margin: 7px auto 5px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: 38px 38px;
          }
          &:nth-child(1){
            .nav-item-icon{
              .bg-image("all_orders");
            }
            &.selected{
              .nav-item-icon{
                .bg-image("all_orders1");
              }
            }
          }
          &:nth-child(2){
            .nav-item-icon{
              .bg-image("obligation");
            }
            &.selected{
              .nav-item-item-icon{
                .bg-image("obligation1");
              }
            }
          }
          &:nth-child(3){
            .nav-item-icon{
              .bg-image("paid");
            }
            &.selected{
              .nav-item-icon{
                .bg-image("paid1");
              }
            }
          }
          &:nth-child(4){
            .nav-item-icon{
              .bg-image("cancel");
            }
            &.selected{
              .nav-item-icon{
                .bg-image("cancel1");
              }
            }
          }
        }
      }
    }
    .content{
      .order-list{
        background-color: #fff;
        padding: 14px 12px 0;
        margin-bottom: 9px;
        .list-top{
          position: relative;
          height: 44px;
          font-size: 0;
          p{
            line-height: 18px;
            font-size: 11px;
            color: #999;
            margin: 0;
            padding: 0;
          }
          .list-top-rigth{
            position: absolute;
            right: 0;
            top: 0;
            height: 35px;
            line-height: 35px;
            font-size: 12px;
            color: #fe6f69;
          }
        }
        .list-main{
          overflow: hidden;
          padding: 8px 0;
          position: relative;
          .border-top-1px-dotted(#d1d1d1); 
          .border-bottom-1px-dotted(#d1d1d1); 
          .list-main-left{
            float: left;
            width: 73px;
            height: 73px;
            padding-right: 13px;
            img{
              display: block;
              width: 100%;
              height: 100%;
            }
          }
          .list-main-right{
            float: left;
            h4.title{
              margin: 0;
              height: 21px;
              line-height: 21px;
              font-size: 14px;
              color: #363636;
              font-weight: normal;
            }
            p{
              margin: 0;
              height: 11px;
              line-height: 11px;
              font-size: 11px;
              color: #666;
              &.passenger{
                margin:11px 0 14px;
              }
            }
          }
          .price{
            position: absolute;
            right: 0;
            bottom: 15px;
            font-size: 12px;
            color: #fe6f69;
            font-weight: bold;
          }
        }
        .list-bottom{
          height: 53px;
          line-height: 53px;
          text-align: right;
          font-size: 0;
          .confirmation{
            font-size: 12px;
            color: #fe6f69;
          }
          a{
            display: inline-block;
            width: 73px;
            height: 29px;
            line-height: 29px;
            text-align: center;
            font-size: 12px;
            color: #fe6f69;
            border: 1px solid #fe6f69;
            border-radius: 5px;
            vertical-align: middle;
            &:first-child{
              margin-right: 9px;
            }
          }
        }
      }
    }
  }
</style>



