<template>
    <div class="confirmation-container">
      <div class="header-wrapper">
        <div class="header">
          <div class="back_to" @click="$router.back(-1)"></div>
          选择类型
        </div>
      </div>
      <div class="main">
        <div class="prodtype">
          <h4 class="title">产品类型</h4>
          <div class="content">
            <div v-for="(item, index) in confirmData.type" class="type">{{item.title}}<i :class="{active:item.isActive}" @click="changeProdType(index)"></i></div>
          </div>
        </div>
        <div class="departure">
          <h4 class="title">出行日期</h4>
          <div class="content">{{calendar3.display}}<i @click="openByDrop($event)"></i></div>
          <transition name="fade">
            <div class="calendar-dropdown" v-if="calendar3.show">
              <v-calendar :zero="calendar3.zero" :lunar="calendar3.lunar" :value="calendar3.value" :begin="calendar3.begin" :end="calendar3.end" @select="calendar3.select"></v-calendar>
            </div>
          </transition>
        </div>
        <div class="quantity">
          <h4 class="title">数量</h4>
          <div class="content">
            <div v-for="(item, index) in price">{{item.desc}}{{item.price}}<v-cart v-on:on-count-change="onCountChange" v-on:count-index="countIndex" :index="index" :count="item.count"></v-cart></div>
          </div>
        </div>
      </div>
      <div class="footer-fixed">
        <router-link :to="{path:'/purchase/purchase',query:{customer:customer}}">立即支付</router-link>
      </div>
    </div>
</template>

<script>
  const confirmData = {
    type: [
      {
        title: "生日快乐至尊版",
        isActive: true
      },
      {
        title: "生日快乐家庭版",
        isActive: false
      },
      {
        title: "生日快乐亲子版",
        isActive: false
      }
    ]
  };
  let price = [
    {
      customer: 'old',
      desc: '老人',
      price: '$100',
      count: 0
    },
    {
      customer: 'adult',
      desc: '成人',
      price: '$120',
      count: 0
    },
    {
      customer: 'child',
      desc: '小孩',
      price: '$80',
      count: 0
    }
  ];
  import vCalendar from '../common/calendar/calendar.vue'
  import vCart from '../common/cart/cart.vue'
  export default {
    name: 'hello',
    data () {
      return {
        confirmData: confirmData,
        price: price,
        clickIndex: 0,
        calendar3: {
          display: "2018/08/06",
          show: false,
          zero: true,
          value: [2018, 2, 16],
          lunar: true,
          select: (val) => {
            this.calendar3.show = false;
            this.calendar3.value = val;
            this.calendar3.display = val.join("/");
          }
        }
      }
    },
    methods: {
      changeProdType (n) {
        this.confirmData.type.forEach((val) => {
          val.isActive = false;
        });
        this.confirmData.type[n].isActive = true;
      },
      openByDrop (e) {
        this.calendar3.show = true;
        e.stopPropagation();
        window.setTimeout(() => {
          let _height = document.getElementsByClassName("departure")[0].offsetTop - 73;
          window.scrollTo(0,_height);
          document.addEventListener("click", (e) => {
            this.calendar3.show = false;
            document.removeEventListener("click", ()=>{}, false)
          }, false);
        },1000);
      },
      onCountChange (val) {
        this.price[this.clickIndex].count = val;
      },
      countIndex (val) {
        this.clickIndex = val;
      }
    },
    computed: {
      customer() {
        let _customer = '';
        this.price.forEach((val)=>{
          _customer += val.customer + '_' + val.count + '-';
        });
        _customer = _customer.substr(0, _customer.length - 1);
        return _customer;
      }
    },
    mounted () {
      let y = new Date().getFullYear(),
          m = new Date().getMonth() + 1,
          d = new Date().getDate();
      this.calendar3.value = [y, m, d];
      m = m < 10 ? '0' + m : m;
      d = d < 10 ? '0' + d : d;
      this.calendar3.display = y + "/" + m + "/" + d;
    },
    components: {
      vCalendar,
      vCart
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
  @import '../../common/less/mixin';
  .confirmation-container{
    padding-top:64px;
    padding-bottom: 55px;
    background-color: #f8f8f8;
    .header-wrapper{
      .header{
        .back_to{
          .bg-image("back");
        }
      }
    }
    .main{
      &>div{
        background-color: #fff;
        border-bottom: 1px solid #e8e8e8;
        margin-top: 9px;
        h4.title{
          width:93%;
          height:44px;
          line-height:44px;
          font-size: 16px;
          font-weight: normal;
          color: #363636;
          margin:0 auto;
          border-bottom: 1px solid #e8e8e8;
        }
        div.content{
          width:93%;
          margin:0 auto;
          padding:15px 0;
          font-size: 14px;
          .type{
            height:21px;
            line-height:21px;
            margin-bottom: 13px;
            &:last-child{
              margin-bottom: 0;
            }
            i{
              float: right;
              width:21px;
              height:21px;
              background-repeat: no-repeat;
              background-size: 21px 21px;
              background-position: center;
              margin-right: 4px;
              .bg-image("Checkbox");
              &.active{
                .bg-image("Check_on");
              }
            }
          }
        }
      }
      .departure{
        .content{
          height:58px;
          line-height:58px;
          padding:0;
          i{
            float: right;
            width:20px;
            height:20px;
            padding:19px;
            background-repeat: no-repeat;
            background-size: 20px 20px;
            background-position: 29px center;
            .bg-image("calendar");
          }
        }
      }
      .quantity{
        .content{
          &>div{
            height:28px;
            line-height:28px;
            margin-bottom: 14px;
            &:last-child{
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
</style>



