<template>
    <div class="good-template">
      <router-link :to="'/detail/detail/'+goods.id">
        <img :src="goods.img" :alt="goods.title" />
        <p class="title">{{goods.title}}</p>
        <p class="desc">{{goods.desc}}</p>
        <p class="place"><i></i><span>{{goods.place}}</span></p>
        <p class="order"><i></i><span>{{goods.order}}</span></p>
        <div class="price">HK${{goods.price}}</div>
        <div class="price-old">HK${{goods.oldprice}}</div>
        <em class="collect" :class="{collected:goods.collected}" @click.prevent="changeCollect()"></em>
      </router-link>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        props: {
            goods: {
                type: Object,
                default () {
                    return {}
                }
            }
        },
        data () {
            return {
            }
        },
        methods: {
          changeCollect () {
            this.goods.collected = !this.goods.collected;
          }
        },
        mounted () {
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
  @import '../../common/less/mixin';
  .good-template{
    width:325px;
    height:290px;
    background-color: #fff;
    position: relative;
    img{
      display: block;
      width:100%;
      height:162px;
    }
    p{
      font-size: 0;
      color: #999;
      padding:0 15px;
      margin:0;
      i{
        display: inline-block;
        vertical-align: top;
        background-repeat: no-repeat;
        margin-right: 10px;
      }
      span{
        font-size: 12px;
      }
      &.title{
        font-size: 15px;
        color: #363636;
        padding:22px 15px 12px;
      }
      &.desc{
        font-size: 12px;
        margin-bottom: 14px;
      }
      &.place{
        margin-bottom: 12px;
        color: #787878;
        i{
          width:9px;
          height:12px;
          background-size: 9px 12px;
          .bg-image("location");
        }
      }
      &.order{
        font-size: 13px;
        color: #666;
        i{
          width:11px;
          height:12px;
          background-size: 11px 12px;
          .bg-image("time");
        }
      }
    }
    .price{
      position: absolute;
      line-height:20px;
      font-size: 20px;
      color: #fe6f69;
      font-weight: bold;
      bottom:17px;
      right:15px;
    }
    .price-old{
      position: absolute;
      line-height:16px;
      font-size: 16px;
      color: #999;
      right:15px;
      bottom:48px;
      text-decoration: line-through;
    }
    em.collect{
      display: inline-block;
      position: absolute;
      width:52px;
      height:52px;
      background-color: #fff;
      border-radius: 50%;
      right:8px;
      bottom:111px;
      box-shadow: 0 0 2px #f1f1f1;
      &:before{
        content: '';
        display: block;
        position: absolute;
        width:28px;
        height:24px;
        background-size: 28px 24px;
        background-repeat: no-repeat;
        .bg-image("collect");
        left:12px;
        top:14px;
      }
      &.collected{
        &:before{
          .bg-image("collect1");
        }
      }
    }
  }
</style>



