<template>
	<div class="settings">
		<div class="header">
			<router-link to="/account/account" class="back_to"><i></i></router-link>
			设置
		</div>
		<div class="content">
			<a href="javascript:;">出行人信息</a>
			<a href="javascript:;">收货人地址</a>
			<a href="javascript:;">繁简体切换</a>
			<div href="javascript:;" class="subscription" :class="{'subscription-true':isSubscription}" ><i class="switch" @click="changeSubscription"></i>订阅电子报</div>
			<a href="javascript:;">清理缓存</a>
		</div>
		<div class="footer sign-out">
			退出登录
		</div>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				isSubscription: true
			}
		},
		methods: {
			changeSubscription () {
				this.isSubscription = !this.isSubscription;
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less">
	@import "../../common/less/mixin.less";
	.settings{
		background-color: #f8f8f8;
		.header{
			border-bottom: 1px solid #e8e8e8;
			margin-bottom: 9px;
			background-color: #fff; 
			height: 64px;
			line-height: 64px;
			text-align: center;
			font-weight: bold;
			color: #363636;
			position: relative;
			.back_to{
				.bg-image("back");
			}
		}
		.content{
			background-color: #fff;
			border-bottom: 1px solid #e8e8e8;
			margin-bottom: 9px;
			a, .subscription{
				display: block;
				height: 48px;
				line-height: 48px;
				text-indent: 13px;
				font-size: 15px;
				color: #363636;
				border-bottom: 1px solid #f2f2f2;
				background-repeat: no-repeat;
				background-size: 8px 13px;
				background-position: 93% center;
				.bg-image("right");
			}
			a:last-child{
				border: none;
			}
			.subscription{
				background:#fff;
				position: relative;
				.switch{
					position: absolute;
					top: 10px;
					right: 10px;
					display: block;
					width: 50px;
					height: 29px;
					background-size: 50px 29px;
					background-repeat: no-repeat;
					.bg-image("Switch");
				}
			}
			.subscription-true{
				.switch{
					.bg-image("Switch_on");
				}
			}
		}
		.footer{
			height: 50px;
			line-height: 50px;
			text-align: center;
			font-size: 15px;
			color: #fe6f69;
			background-color: #fff;
		}
	}
</style>


