<template>
	<div class="management">
		<div class="header">
			<router-link to="/account/management" class="back_to"><i></i></router-link>
			修改昵称
		</div>
		<div class="content">
			<input type="text" class="username" :value="username" />
			<i class="clear-username" @click="clearName()"></i>
		</div>
		<div class="footer">
			<div class="footer-confirm-btn">确定</div>
		</div>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				username: 'harris'
			}
		},
		methods: {
			clearName () {
				this.username = '';
			}
		}
	}
</script>

<style lang="less" rel="stylesheet/less" scoped>
	@import "../../common/less/mixin.less";
	.management{
		background-color: #f8f8f8;
		.header{
			border-bottom: 1px solid #e8e8e8;
			margin-bottom: 9px;
			background-color: #fff; 
			height: 64px;
			line-height: 64px;
			text-align: center;
			font-weight: bold;
			color: #363636;
			position: relative;
			.back_to{
				.bg-image("back");
			}
		}
		.content{
			background-color: #fff;
			height: 48px;
			line-height: 48px;
			padding: 0 24px;
			position: relative;
			input{
				width: 100%;
				height: 100%;
				border: none;
				outline: none;
				font-size: 15px;
				color: #999;
				padding: 0;
			}
			i.clear-username{
				position: absolute;
				top: 17px;
				right: 14px;
				display: block;
				width: 14px;
				height: 14px;
				background-size: 14px 14px;
				background-repeat: no-repeat;
				.bg-image("close");
			}
		}
	}
</style>


