<template>
  <div class="account-wrapper">
    <div class="account">
      <div class="header">
        <div class="header-top">个人中心</div>
        <div class="avatar">
          <img :src="accountInfo.avatar" >
          <span>{{accountInfo.name}}</span>
        </div>
      </div>
      <div class="content">
        <div class="my-center">
          <a href="javascript:;" class="collection"><i></i>我的收藏</a>
          <a href="javascript:;" class="comment"><i></i>我的评论</a>
          <a href="javascript:;" class="preferential"><i></i>我的优惠</a>
          <a href="javascript:;" class="invite"><i></i>邀请好友</a>
        </div>
        <div class="my-management">
          <router-link class="management" to="/account/management"><i></i>账号管理</router-link>
          <a href="javascript:;" class="issues"><i></i>常见问题</a>
          <a href="javascript:;" class="customer-service"><i></i>联系客服</a>
        </div>
        <div class="my-setting">
          <router-link class="setting" to="/account/setting"><i></i>设置</router-link>
        </div>
      </div>
      <v-footer></v-footer>
    </div>
  </div>
</template>

<script>
  import vFooter from '../common/footer/footer';
  let baseData = {
    account: {
      "name": "harris",
      "avatar": "https://static.vux.li/demo/2.jpg"
    }
  }
  export default {
    name: 'account',
    data () {
      return {
        accountInfo: baseData.account
      }
    },
    components: {
      vFooter
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
  @import "../../common/less/mixin.less";
  .account-wrapper{
    padding-bottom: 57px;
    .account{
      height: 100%;
      background-color: #f8f8f8;
      .header{
        .header-top, .avatar{
          border-bottom: 1px solid #e8e8e8;
          margin-bottom: 9px;
          background-color: #fff; 
        }
        .header-top{
          height: 64px;
          line-height: 64px;
          text-align: center;
          font-weight: bold;
          color: #363636;
        }
        .avatar{
          padding: 29px 0;
          text-align: center;
          font-size: 0;
          img{
            display: block;
            width: 71px;
            height: 71px;
            border-radius: 50%;
            margin: 0 auto;
          }
          span{
            display: block;
            text-align: center;
            font-size: 19px;
            padding-top: 15px;
            color: #fe6f69;
            line-height: 1;
          }
        }
      }
      .content{
        &>div{
          background-color: #fff;
          border-bottom: 1px solid #e8e8e8;
          margin-bottom: 9px;
        }
        a{
          display: block;
          height: 48px;
          line-height: 48px;
          font-size: 15px;
          color: #363636;
          border-bottom: 1px solid #f2f2f2;
          background-repeat: no-repeat;
          background-size: 8px 13px;
          background-position: 93% center;
          .bg-image("right");
          &:last-child{
            border: none;
          }
          i{
            display: inline-block;
            vertical-align: top;
            background-repeat: no-repeat;
            margin:0 14px 0 16px;
          }
        }
        .my-center{
          a:nth-child(1){
            i{
              width: 17px;
              height: 14px;
              background-size: 17px 14px;
              .bg-image("collect");
              margin-top: 17px;
            }
          }
          a:nth-child(2){
            i{
              width: 17px;
              height: 14px;
              background-size: 17px 14px;
              .bg-image("comments");
              margin-top: 17px;
            }
          }
          a:nth-child(3){
            i{
              width: 17px;
              height: 17px;
              background-size: 17px 17px;
              .bg-image("coupon");
              margin-top: 15px;
            }
          }
          a:nth-child(4){
            i{
              width: 16px;
              height: 12px;
              background-size: 16px 12px;
              .bg-image("invite");
              margin-top: 18px;
              margin-left: 17px;
            }
          }
        }
        .my-management{
          a:nth-child(1){
            i{
              width: 18px;
              height: 18px;
              background-size: 18px 18px;
              .bg-image("account-");
              margin-top: 15px;
              margin-left: 15px;
            }
          }
          a:nth-child(2){
            i{
              width: 18px;
              height: 17px;
              background-size: 18px 17px;
              .bg-image("problem");
              margin-top: 15px;
              margin-left: 15px;
            }
          }
          a:nth-child(3){
            i{
              width: 17px;
              height: 17px;
              background-size: 17px 17px;
              .bg-image("phone");
              margin-top: 15px;
            }
          }
        }
        .my-setting i{
          width: 17px;
          height: 17px;
          background-size: 17px 17px;
          .bg-image("set");
          margin-top: 15px;
        }
      }
    }
  }
    
</style>



