<template>
  <div class="dest">
    <div class="header">
      <div class="header-top">目的地</div>
      <div class="search-wrapper">
        <div class="search-bar">搜索目的地、景点、主题</div>
      </div>
    </div>
    <div class="nav">
      <swiper :options="areaSlideOption">
        <swiper-slide v-for="item in areaList">
          <mt-navbar v-model="areaSlideActive">
            <mt-tab-item  :id="item.id">
              {{item.name}}
            </mt-tab-item>
          </mt-navbar>
        </swiper-slide>
      </swiper>
    </div>
    <div class="content">
      <div class="hot-dest">
        <h3 class="title">热门目的地</h3>
        <div class="container">
          <swiper :options="hotDestOption">
            <swiper-slide v-for="item in hotDestList">
              <img :src="item.img" alt="item.name">
              <span>{{item.name}}</span>
            </swiper-slide>
          </swiper>
        </div>
      </div>
      <div class="all-dest">
        <h3 class="title">全部目的地</h3>
        <div class="container">
          <div class="list" v-for="item in allDestList">
            <h4 class="continent">{{item.continent}}</h4>
            <dl v-for="country in item.data">
              <dt @click="country.isShow = !country.isShow">{{country.country}}<i class="arrow" :class="{up:country.isShow}"></i></dt>
              <transition-group name="isShow">
                <dd v-show="country.isShow" v-for="city in country.data" :key="city">{{city.city}}</dd>
              </transition-group>
            </dl>
          </div>

        </div>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper';
  let destData = {
      "areas": [
        {
          "id": "world",
          "name": "全球"
        },
        {
          "id": "asia",
          "name": "亚洲"
        },
        {
          "id": "africa",
          "name": "非洲"
        },
        {
          "id": "europe",
          "name": "欧洲"
        },
        {
          "id": "north_america",
          "name": "北美洲"
        },
        {
          "id": "south_america",
          "name": "南美洲"
        },
        {
          "id": "oceania",
          "name": "大洋洲"
        }
      ],
      "hotDestList": [
        {
          "name": "北京",
          "img" : "https://static.vux.li/demo/2.jpg"
        },
        {
          "name": "上海",
          "img" : "https://static.vux.li/demo/1.jpg"
        },
        {
          "name": "广州",
          "img" : "https://static.vux.li/demo/2.jpg"
        },
        {
          "name": "深圳",
          "img" : "https://static.vux.li/demo/1.jpg"
        }
      ],
      "allDestList": [
        {
            "continent": "东南亚",
            "data": [
              {
                  "country": "泰国",
                  "isShow": false,
                  "data": [
                    {
                        "city": "曼谷",
                        "id": "BKK"
                    },
                    {
                      "city": "清迈",
                      "id": "Chiengmai"
                    }
                  ]
              }
            ]
        },
        {
          "continent": "亚洲",
          "data": [
            {
              "country": "中国",
              "isShow": false,
              "data": [
                {
                  "city": "北京",
                  "id": "Beijing"
                },
                {
                  "city": "上海",
                  "id": "Shanghai"
                }
              ]
            },
            {
              "country": "韩国",
              "isShow": false,
              "data": [
                {
                  "city": "首尔",
                  "id": "Shouer"
                },
                {
                  "city": "釜山",
                  "id": "Fushan"
                }
              ]
            }
          ]
        },
        {
          "continent": "欧洲",
          "data": [
            {
              "country": "法国",
              "isShow": false,
              "data": [
                {
                  "city": "巴黎",
                  "id": "Paris"
                }
              ]
            }
          ]
        }
      ]
  }
  import vFooter from '../common/footer/footer';
  export default {
    name: 'dest',
    data () {
      return {
        areaList: destData.areas,
        areaSlideActive: 'world',
        areaSlideOption: {
          slidesPerView: 'auto'
        },
        hotDestList: destData.hotDestList,
        hotDestOption: {
          slidesPerView: 'auto'
        },
        allDestList: destData.allDestList
      }
    },
    components: {
      vFooter,
      swiper,
      swiperSlide
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">
  @import "../../common/less/mixin.less";
  .dest{
    padding-bottom: 57px;
    background-color: #fff;
    .header{
      .header-top{
        height:64px;
        line-height:64px;
        text-align: center;
        font-size: 16px;
        font-weight: bold;
        color: #363636;
      }
      .search-wrapper{
        height:36px;
        background-color: #f8f8f8;
        border-top: 1px solid #dfdfdf;
        padding:13px 0;
        position: static;
        .search-bar{
          .bg-image("search-");
        }
      }
    }
    .nav{
      height: 70px;
      margin-bottom: 30px;
      .swiper-container{
        height: 70px;
        .swiper-slide{
          width: 70px;
          margin: 0 12px;
          &:nth-child(1){
            .mint-tab-item-icon{
              width: 19px;
              height: 19px;
              background-size: 19px 19px;
              .bg-image("world");
              margin-top: 17px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("world1");
              }
            }
          }
          &:nth-child(2){
            .mint-tab-item-icon{
              width: 22px;
              height: 19px;
              background-size: 22px 19px;
              .bg-image("Asia");
              margin-top: 17px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("Asia1");
              }
            }
          }
          &:nth-child(3){
            .mint-tab-item-icon{
              width: 27px;
              height: 15px;
              background-size: 27px 15px;
              .bg-image("Africa");
              margin-top: 21px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("Africa1");
              }
            }
          }
          &:nth-child(4){
            .mint-tab-item-icon{
              width: 12px;
              height: 22px;
              background-size: 12px 22px;
              .bg-image("Europe");
              margin-top: 14px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("Europe1");
              }
            }
          }
          &:nth-child(5){
            .mint-tab-item-icon{
              width: 24px;
              height: 19px;
              background-size: 24px 19px;
              .bg-image("North_America");
              margin-top: 17px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("North_America1");
              }
            }
          }
          &:nth-child(6){
            .mint-tab-item-icon{
              width: 22px;
              height: 22px;
              background-size: 22px 22px;
              .bg-image("South_America");
              margin-top: 14px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("South_America1");
              }
            }
          }
          &:nth-child(7){
            .mint-tab-item-icon{
              width: 25px;
              height: 16px;
              background-size: 25px 16px;
              .bg-image("Oceania");
              margin-top: 20px;
            }
            .mint-tab-item.is-selected{
              .mint-tab-item-icon{
                .bg-image("Oceania1");
              }
            }
          }
        }
      }
      .mint-navbar{
        height: 70px;
        .mint-tab-item{
          margin: 0;
          padding: 0;
          &.is-selected{
            border-bottom: 2px solid #fe6f69;
            .mint-tab-item-label{
              color: #fe6f69;
            }
          }
          .mint-tab-item-icon{
            display: block;
            margin: 0 auto 10px;
            padding: 0;
            background-repeat: no-repeat;
          }
          .mint-tab-item-label{
            color: #666;

          }
        }
      }
    }
    .content{
      h3.title{
        line-height: 15px;
        font-size: 15px;
        color: #363636;
        margin:0 0 13px 0;
        text-indent: 12px;
        font-weight: normal;
      }
      .hot-dest{
        .container{
          height:85px;
          overflow: hidden;
          margin-bottom: 30px;
          padding:0 0 0 10px;
          .swiper-slide{
            width:125px;
            height:85px;
            margin-right: 10px;
            img{
              display: block;
              width:100%;
              height:63px;
            }
            span{
              display: inline-block;
              height:13px;
              line-height:13px;
              font-size: 13px;
              color: #666666;
              margin-top: 9px;
            }
          }
        }
      }
      .all-dest{
        h3.title{
          margin-bottom: 20px;
        }
        .container{
          width:98%;
          margin:0 auto;
          h4.continent{
            height:30px;
            line-height:30px;
            font-size: 12px;
            color: #666;
            background-color: #f0f0f0;
            margin:0;
            text-indent: 10px;
            font-weight: normal;
          }
          .list{
            dl{
              width:96%;
              margin:0 auto;
              border-bottom: 1px solid #dfdfdf;
              &:last-child{
                border:none;
              }
              dt,dd{
                height:44px;
                line-height:44px;
                font-size: 14px;
                width: 94%;
                margin:0 auto;
              }
              dt{
                color: #363636;
                i.arrow{
                  float: right;
                  width: 9px;
                  height: 5px;
                  background-repeat: no-repeat;
                  background-size: 9px 5px;
                  .bg-image("down");
                  margin: 19px 10px 0 0;
                  &.up{
                    .bg-image("up");
                  }
                }
              }
              dd{
                text-indent: 12px;
                color: #666;
                .border-top-1px-dotted(#dfdfdf;)
              }
              .isShow-enter-active, .isShow-leave-active{
                transition: all .5s;
              }
              .isShow-enter, .isShow-leave-to{
                height: 0;
                opacity: 0;
              }
            }
          }
        }
      }
    }
  }
</style>



