<template>
    <div class="cart">
      <i class="minus" @click="minusCount()"></i>
      <span>{{myCount}}</span>
      <i class="plus" @click="plusCount()"></i>
    </div>
</template>

<script>
    export default {
      name: 'cart',
      data () {
        return {
          myCount: this.count
        }
      },
      methods: {
        plusCount () {
          this.myCount++;
          this.$emit('count-index', this.index);
        },
        minusCount () {
          if(this.myCount > 0){
            this.myCount--;
          }
          this.$emit('count-index', this.index);
        }
      },
      watch: {
        count(val) {
          this.myCount = val;
        },
        myCount(val) {
          this.$emit('on-count-change', val);
        }
      },
      props: {
       count: {
         type: Number,
         dafault () {
           return 0;
         }
       },
       index: {
         type: Number,
         dafault () {
           return 0;
         }
       }
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
  @import '../../../common/less/mixin';
  .cart{
    float: right;
    height:28px;
    line-height:28px;
    overflow: hidden;
    i{
      display: inline-block;
      width:32px;
      height:28px;
      background-repeat: no-repeat;
      background-size: 32px 28px;
      background-position: center;
      &.minus{
        .bg-image("-")
      }
      &.plus{
        .bg-image("+")
      }
    }
    span{
      display: inline-block;
      width:37px;
      height:28px;
      line-height:28px;
      text-align: center;
      font-size: 14px;
      background-color: #f5f5f5;
      vertical-align: top;
    }
  }
</style>



