import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index/index'
import Dest from '@/components/dest/dest'
import Order from '@/components/order/order'
import Detail from '@/components/detail/detail'
import Confirmation from '@/components/purchase/confirmation'
import Purchase from '@/components/purchase/purchase'
import Traveller from '@/components/purchase/traveller'
import Account from '@/components/account/account'
import Setting from '@/components/account/setting'
import Management from '@/components/account/management'
import Changeusername from '@/components/account/changeusername'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/dest/dest',
      name: 'dest',
      component: Dest
    },
    {
      path: '/order/order',
      name: 'order',
      component: Order
    },
    {
      path: '/account/account',
      name: 'account',
      component: Account
    },
    {
      path: '/account/setting',
      name: 'setting',
      component: Setting
    },
    {
      path: '/account/management',
      name: 'management',
      component: Management
    },
    {
      path: '/account/changeusername',
      name: 'changeusername',
      component: Changeusername
    },
    {
      path: '/detail/detail/:id',
      name: 'detail',
      component: Detail
    },
    {
      path: '/purchase/confirmation/:id',
      name: 'confirmation',
      component: Confirmation
    },
    {
      path: '/purchase/purchase',
      name: 'purchase',
      component: Purchase
    },
    {
      path: '/purchase/traveller',
      name: 'traveller',
      component: Traveller
    },
    {
      path: '*',
      redirect: '/index'
    }


  ]
})



