<template>
    <div class="purchase-container">
      <div class="header-wrapper">
        <div class="header">
          <div class="back_to" @click="$router.back(-1)"></div>
          确认订单
        </div>
        <div class="prod-list-container">
          <div class="prod-list">产品清单<i class="down"></i><span class="total-price">{{totalPrice}}</span></div>
        </div>
      </div>
      <div class="main-content">
        <div class="travellers">
          <router-link to="/purchase/traveller" v-if="customer.oldCount>0" v-for="n in customer.oldCount">出行人{{n}}<span>老人</span></router-link>
          <router-link to="/purchase/traveller" v-if="customer.adultCount>0" v-for="n in customer.adultCount">出行人{{customer.oldCount + n}}<span>成人</span></router-link>
          <router-link to="/purchase/traveller" v-if="customer.childCount>0" v-for="n in customer.childCount">出行人{{customer.oldCount + customer.adultCount + n}}<span>小孩</span></router-link>
        </div>
        <div class="emails">
          <input type="email" placeholder="请输入电子邮箱">
        </div>
        <div class="phones">
          <input type="number" placeholder="请输入电话号码">
        </div>
        <div class="posts">
          <div>是否邮寄<span>($8)</span><input class="switch" type="checkbox"></div>
        </div>
      </div>
      <div class="footer-fixed">
        <a href="javascript:;">确认支付</a>
      </div>
    </div>
</template>

<script>
    export default {
      name: 'purchase',
      data () {
        return {
          travelCount: 0,
          customer: {
            oldPrice: 100,
            oldCount: 0,
            adultPrice: 120,
            adultCount: 0,
            childPrice: 80,
            childCount: 0
          }
        }
      },
      computed: {
        totalPrice() {
          return "$" + (this.customer.oldPrice * this.customer.oldCount + this.customer.adultPrice * this.customer.adultCount + this.customer.childPrice * this.customer.childCount);
        }
      },
      mounted() {
        let customerData = this.$route.query.customer.split("-");
        this.customer.oldCount = customerData[0].split("_")[1] * 1;
        this.customer.adultCount = customerData[1].split("_")[1] * 1;
        this.customer.childCount = customerData[2].split("_")[1] * 1;
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
  @import '../../common/less/mixin';
  .purchase-container{
    background-color: #f8f8f8;
    padding-top:115px;
    padding-bottom: 55px;
    .header-wrapper{
      height:108px;
      .back_to{
        .bg-image("back");
      }
      .prod-list-container{
        height:44px;
        line-height:44px;
        background-color: #fe6f69;
        font-size: 14px;
        color: #fff;
        .prod-list{
          width:93%;
          margin:0 auto;
          background-repeat: no-repeat;
          background-position: 0 center;
          background-size: 16px 19px;
          .bg-image("list");
          text-indent: 24px;
        }
        span.total-price{
          float: right;
          margin-right: 12px;
        }
        i.down{
          float: right;
          width:14px;
          height:8px;
          background-repeat: no-repeat;
          background-size: 14px 8px;
          background-position: center;
          .bg-image("down");
          margin-top: 18px;
        }
      }
    }
    .main-content{
      &>div{
        border-bottom: 1px solid #e8e8e8;
        margin-bottom: 9px;
        background-color: #fff;
        font-size: 14px;
        &:not(:first-child){
          height:50px;
          line-height:50px;
        }
        &>div{
          width:93%;
          margin:0 auto;
        }
      }
      .travellers{
        a{
          display: block;
          height:50px;
          line-height:50px;
          width:92%;
          margin:0 auto;
          &:not(:last-child){
            .border-bottom-1px-dotted(#e8e8e8;)
          }
          background-repeat: no-repeat;
          background-size: 8px 14px;
          background-position: right center;
          .bg-image("right");
          span{
            color: #999;
            margin-left: 8px;
          }
        }
      }
      .emails, .phones{
        color: #999;
        input{
          display: block;
          width:93%;
          height:50px;
          line-height:50px;
          margin:0 auto;
          padding:0;
          border:none;
          outline:none;
        }
      }
      .posts{
        span{
          color: #fe6f69;
        }
      }
    }
  }
</style>



