<template>
	<div class="management">
		<div class="header">
			<router-link to="/account/account" class="back_to"><i></i></router-link>
			账号管理
		</div>
		<div class="content">
			<router-link to="/account/changeusername">修改资料</router-link>
			<a href="javascript:;">修改资料</a>
			<a href="javascript:;">修改密码</a>
			<a href="javascript:;" class="bind-phone">绑定手机号码<span v-if="phoneNumber !== ''">{{phoneNumber}}</span></a>
		</div>
	</div>
</template>
<script>
	export default {
		data () {
			return {
				phoneNumber: 13623468905
			}
		},
		methods: {
			
		}
	}
</script>

<style lang="less" rel="stylesheet/less" scoped>
	@import "../../common/less/mixin.less";
	.management{
		background-color: #f8f8f8;
		.header{
			border-bottom: 1px solid #e8e8e8;
			margin-bottom: 9px;
			background-color: #fff; 
			height: 64px;
			line-height: 64px;
			text-align: center;
			font-weight: bold;
			color: #363636;
			position: relative;
			.back_to{
				.bg-image("back");
			}
		}
		.content{
			background-color: #fff;
			border-bottom: 1px solid #e8e8e8;
			margin-bottom: 9px;
			a{
				display: block;
				height: 48px;
				line-height: 48px;
				text-indent: 13px;
				font-size: 15px;
				color: #363636;
				border-bottom: 1px solid #f2f2f2;
				background-repeat: no-repeat;
				background-size: 8px 13px;
				background-position: 93% center;
				.bg-image("right");
				&.bind-phone{
					border: none;
					position: relative;
					span{
						position: absolute;
						right: 12%;
						font-size: 12px;
						color: #999;
					}
				}
			}
		}
	}
</style>


